import styled from "styled-components";

export const FooterContainer = styled.div`
  display: grid;
  height: 700px;
  grid-template-rows: 1fr 1fr 1fr;
  @media (min-width: 700px) {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    height: auto;
    margin-top: 180px;
  }
`;

export const FooterCard = styled.div`
  width: 100%;
  background: #f1f1f1;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
  svg {
    width: 30px;
    height: 30px;
  }
  h3 {
    margin-top: 10px;
  }
  a {
    margin-top: 15px;
    display: flex;
    align-items: center;
    font-weight: bold;
    color: #00587a;
  }

  p {
    font-size: 0.9rem;
    margin-top: 30px;
  }
  em {
    font-weight: bold;
  }
`;

export const Newsletter = styled.div`
  width: 100%;
  background-color: #aee6e6;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  h3 {
    margin-bottom: 10px;
  }
  input {
    padding: 10px;
    width: 60%;
    outline: none;
    border: none;
    border-radius: 5px 0 0 5px;
  }
  button {
    background-color: #16a596;
    padding: 10px;
    color: white;
    outline: none;
    border: none;
    border-radius: 0 5px 5px 0;
    font-weight: bold;
  }
  div {
    width: 100%;
    display: flex;
    justify-content: center;
  }
`;

export const LinksContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  width: 100%;
  height: auto;
  box-sizing: border-box;

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 30px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    text-align: center;
    color: #222831;
  }
`;

export const SocialIcons = styled.div`
  width: 100%;
  padding: 30px;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  svg {
    color: gray;
    width: 25px;
    height: 25px;
  }
`;
