import React from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import ThemeContext from "../ThemeContext";
import useThemeContext from "../hooks/useThemeContext";

import Home from "../containers/Home/index";
import Details from "../containers/Details/index";
import Cart from "../containers/Cart/index";
import Payment from "../containers/Payment/index";
import Information from "../containers/Information/index";
import Success from "../containers/Success/index";
import Checkout from "../containers/Checkout/index";
import NotFound from "../containers/NotFound/index";

function App() {
  const Context = useThemeContext();

  return (
    <ThemeContext.Provider value={Context}>
      <BrowserRouter>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/code-store/details/:itemId" component={Details} />
          <Route
            exact
            path="/code-store/checkout/success"
            component={Success}
          />
          <Route exact path="/code-store/checkout" component={Checkout} />
          <Route exact path="/code-store" component={Home} />
          <Route component={NotFound} />
        </Switch>
      </BrowserRouter>
    </ThemeContext.Provider>
  );
}

export default App;
