import styled from "styled-components";

export const DetailsContainer = styled.div`
  margin-top: 60px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  height: auto;
  @media (max-width: 700px) {
    grid-template-columns: 1fr;
    margin-bottom: 80px;
  }
  margin-bottom: -60px;
  button {
    cursor: pointer;
  }
  
  }
`;

export const ProductImage = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  img {
    width: 75%;
    height: 620px;
    object-fit: cover;
    @media (max-width: 700px) {
      width: 70%;
      height: 550px;
      margin: 0 auto;
    }
    @media (max-width: 500px) {
      width: 80%;
      height: 430px;
    }
    @media (max-width: 400px) {
      width: 70%;
      height: 350px;
    }
  }
`;

export const ProductInfo = styled.div`
  width: 70%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  @media (max-width: 700px) {
    width: 80%;
    margin-top: 50px;
  }
`;

export const Description = styled.span`
  font-size: 1.1rem;
  margin-top: 20px;
`;

export const Autor = styled.span`
  font-size: 0.9rem;
  text-decoration: underline;
`;

export const Price = styled.span`
  margin-top: 20px;
  font-size: 2rem;
  font-weight: bold;
`;

export const Color = styled.div`
  margin-top: 20px;
  em {
    font-weight: bold;
  }
  div {
    margin-top: 10px;
    background-color: black;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 2px solid #74c7b8;
  }
`;

export const Size = styled.div`
  margin-top: 30px;
`;

export const SizeOption = styled.span`
  margin-top: 15px;
  border: 1px solid #222831;
  padding: 10px 20px;
  text-align: center;
  display: inline-block;
  margin-right: 10px;
`;

export const Type = styled.span`
  margin-top: 20px;
`;

export const Cart = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50px;
  background-color: #008891;
  color: white;
  border: none;
  outline: none;
  padding: 10px 15px;
  box-sizing: border-box;
  margin: 0 auto;
  margin-top: 30px;
  border-radius: 2px;
  font-size: 1.1rem;
  svg {
    width: 25px;
    height: 25px;
    margin-right: 10px;
  }
`;

export const ButtonCont = styled.div`
  width: 100%;
  margin-top: 10px;
  height: 50px;
  display: flex;
  justify-content: space-between;
`;

export const Fav = styled.button`
  width: 87%;
  height: 50px;
  display: inline-block;
  background-color: white;
  border: none;
  border: 1px solid #cdd0cb;
  outline: none;
  box-sizing: border-box;
  border-radius: 2px;
  font-size: 1.1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    width: 25px;
    height: 25px;
    margin-right: 5px;
  }
`;
export const Share = styled.button`
  height: 50px;
  background-color: white;
  border: none;
  border: 1px solid #cdd0cb;
  outline: none;
  padding: 10px 15px;
  box-sizing: border-box;
  border-radius: 2px;
  svg {
    width: 25px;
    height: 25px;
  }
`;

export const Time = styled.div`
  margin-top: 15px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  svg {
    margin-left: 10px;
  }
`;
