import React from "react";
import styled from "styled-components";

import { FiPlusCircle } from "react-icons/fi";
import Card from "../Card/index";

const CatContainer = styled.div`
  padding: 20px 0px 100px;
  h1 {
    text-align: center;
    margin: 0 auto;
    font-size: 1.5rem;
  }
  @media (min-width: 600px) {
    margin-top: 140px;
    height: 600px;
    box-sizing: border-box;
    h1 {
      font-size: 1.8rem;
      margin-bottom: 80px;
    }
  }
`;

const CatContainerDetails = styled.div`
  padding: 20px 0px 40px;
  @media (min-width: 600px) {
    margin-top: 140px;
    margin-bottom: -80px;
    height: 500px;
    box-sizing: border-box;
  }
`;

const CardContainer = styled.div`
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  justify-content: center;
  align-items: center;
  @media (min-width: 600px) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    margin-top: 0px;
    overflow: scroll;
    ::-webkit-scrollbar {
      display: none;
    }
  }
`;

const PlusIcon = styled(FiPlusCircle)`
  margin: 0 auto;
  margin-top: 50px;
  display: block;
  text-align: center;
  height: 25px;
  width: 25px;
`;

function Categories({ items, title, detailsPage, id }) {
  const fourItems = items.filter((item) => items.indexOf(item) < 4);

  if (detailsPage) {
    return (
      <CatContainerDetails>
        <CardContainer>
          {fourItems.map((item) => {
            return <Card item={item} key={item.id} detailsPage={detailsPage} />;
          })}
        </CardContainer>
        <PlusIcon />
      </CatContainerDetails>
    );
  }

  return (
    <CatContainer id={id}>
      {title ? <h1>{items[0].category}</h1> : null}
      <CardContainer>
        {fourItems.map((item) => {
          return <Card item={item} key={item.id} />;
        })}
      </CardContainer>
      <PlusIcon />
    </CatContainer>
  );
}

export default Categories;
