import React, { useEffect, useContext } from "react";
import { useHistory } from "react-router-dom";
import { FaShoppingBag } from "react-icons/fa";
import { AiFillLock } from "react-icons/ai";

import ProductModal from "../ProductModal/index";
import ThemeContext from "../../ThemeContext";

import { Background, ModalContainer, Head, Cart, Footer } from "./styles";

function Modal({ closeModal }) {
  const { state, handleAddProductQuantity, reloadProductsQ } = useContext(
    ThemeContext
  );
  const { cart } = state;
  const { productsQ } = state;

  const history = useHistory();
  useEffect(() => {
    reloadProductsQ();
  }, []);

  const totalAmount =
    productsQ.reduce((acc, item) => {
      return item.price * item.quantity + acc;
    }, 0) || 0;

  return (
    <>
      <Background />
      <ModalContainer>
        <Head>
          <h2>
            <FaShoppingBag />
            YOUR CART
          </h2>
          <button onClick={closeModal}>X</button>
        </Head>
        <Cart>
          {cart.map((item) => (
            <ProductModal
              item={item}
              key={item.id}
              productsQ={productsQ}
              handleAddProductQuantity={handleAddProductQuantity}
            />
          ))}
        </Cart>
        <Footer>
          <div>
            <span>Subtotal</span>
            <span>{`$${totalAmount}`}</span>
          </div>
          <span>Taxes and shipping calculated at checkout</span>
          <button
            onClick={() => {
              history.push("/checkout");
            }}
          >
            <AiFillLock />
            CHECK OUT
          </button>
        </Footer>
      </ModalContainer>
    </>
  );
}

export default Modal;
