import React, { useEffect, useContext } from "react";

import Layout from "../../components/Layout/index";
import { AiOutlineShoppingCart, AiOutlineShareAlt } from "react-icons/ai";
import { MdFavoriteBorder } from "react-icons/md";
import { FiTruck } from "react-icons/fi";

import Categories from "../../components/Categories/index";

import ThemeContext from "../../ThemeContext";

import {
  DetailsContainer,
  ProductImage,
  ProductInfo,
  Description,
  Autor,
  Price,
  Color,
  Size,
  SizeOption,
  Type,
  Cart,
  ButtonCont,
  Fav,
  Share,
  Time,
} from "./styles";

function Details() {
  const { state, addToCart, addToFavs } = useContext(ThemeContext);
  const { products } = state;

  const id = window.location.pathname.replace("/details/", "").slice(0, -2);
  const numberId = window.location.pathname.replace("/details/", "").slice(-1);
  const index = Object.keys(products).indexOf(id);
  const firstCat = Object.values(products)[0];
  const cat = Object.values(products)[index];
  const nextCat = Object.values(products)[index + 1];
  const item = cat[numberId - 1];

  useEffect(() => {
    const scrollUp = () => {
      window.scrollTo({ top: 0 });
    };
    scrollUp();
  });

  return (
    <Layout>
      <DetailsContainer>
        <ProductImage>
          <img src={item.image} alt="" />
        </ProductImage>
        <ProductInfo>
          <h1>{item.title}</h1>
          <span>4</span>
          <Description>
            While awake I code funny motivational quote gift
          </Description>
          <Autor>By acerohernan</Autor>
          <Price>{`$ ${item.price}`}</Price>
          <a href="#">Plus shipping</a>
          <Color>
            <span>
              <em>Color:</em> black
            </span>
            <div />
          </Color>
          <Size>
            <span>Choose a size</span>
            <div>
              <SizeOption>S</SizeOption>
              <SizeOption>M</SizeOption>
              <SizeOption>L</SizeOption>
              <SizeOption>XL</SizeOption>
              <SizeOption>2XL</SizeOption>
            </div>
          </Size>
          <Type>Regular fit</Type>
          <Cart
            onClick={() => {
              addToCart(item);
            }}
          >
            <AiOutlineShoppingCart />
            Add to cart
          </Cart>
          <ButtonCont>
            <Fav
              onClick={() => {
                addToFavs(item);
              }}
            >
              <MdFavoriteBorder />
              Add to favorites
            </Fav>
            <Share>
              <AiOutlineShareAlt />
            </Share>
          </ButtonCont>
          <Time>
            Shipping time
            <FiTruck />
          </Time>
        </ProductInfo>
      </DetailsContainer>
      <Categories items={cat} detailsPage />
      {item.category === "Masks" ? (
        <Categories items={firstCat} detailsPage />
      ) : (
        <Categories items={nextCat} detailsPage />
      )}
    </Layout>
  );
}

export default Details;
