import React, { useContext, useState } from "react";
import { Product, Image, Title, ProductInfo, Price, Delete } from "./styles";

import { BsFillTrashFill } from "react-icons/bs";
import ThemeContext from "../../ThemeContext";

function ProductModal({ item, checkout, handleAddProductQuantity }) {
  const { image, title, price } = item;

  const { deleteToCart } = useContext(ThemeContext);

  const [quantity, setQuantity] = useState(1);

  const handleAddQuantity = () => {
    if (quantity < 4) {
      setQuantity(quantity + 1);
    }
    handleAddProductQuantity({
      id: item.id,
      quantity: quantity < 4 ? quantity + 1 : quantity,
      price: item.price,
      image: item.image,
      title: item.title,
    });
  };

  const handleReduceQuantity = () => {
    if (quantity > 1) {
      setQuantity(quantity - 1);
    }
    handleAddProductQuantity({
      id: item.id,
      quantity: quantity > 1 ? quantity - 1 : quantity,
      price: item.price,
      image: item.image,
      title: item.title,
    });
  };

  return (
    <Product>
      <Image src={image} alt="" />

      <Title>{title}</Title>
      <ProductInfo>
        {checkout ? (
          <span>{`Quantity: ${item.quantity}`}</span>
        ) : (
          <div>
            <button onClick={handleReduceQuantity}>-</button>
            <span>{quantity}</span>
            <button onClick={handleAddQuantity}>+</button>
          </div>
        )}

        <Price>{`$${price * quantity}`}</Price>
      </ProductInfo>
      <Delete
        onClick={() => {
          deleteToCart(item);
        }}
      >
        <BsFillTrashFill />
      </Delete>
    </Product>
  );
}

export default ProductModal;
