import React, { useContext, useState } from "react";
import ReactDOM from "react-dom";
import { Link } from "react-router-dom";
import { FiMenu, FiSearch, FiShoppingCart } from "react-icons/fi";
import { MdFavoriteBorder } from "react-icons/md";

import ThemeContext from "../../ThemeContext";

import Logo from "../Logo/index";
import Modal from "../Modal/index";
import { Container, Orders, Favs, Icons, Links } from "./styles";

function Header() {
  const { state } = useContext(ThemeContext);
  const { cart, favs } = state;

  const [openCart, setOpenCart] = useState(false);

  const handleOpenCart = () => {
    setOpenCart(true);
  };

  const handleCloseCart = () => {
    setOpenCart(false);
  };

  return (
    <Container>
      <Icons className="hidden-600px">
        <FiMenu />
        <FiSearch />
      </Icons>
      <Link to="/">
        <Logo />
      </Link>

      <Links>
        <Link to="/">Home</Link>
        <Link to="/details">Categories</Link>
        <Link to="#">Contact Us</Link>
      </Links>
      <Icons>
        <button>
          <MdFavoriteBorder />
        </button>
        {favs.length > 0 ? <Favs>{favs.length}</Favs> : null}
        <button onClick={handleOpenCart}>
          <FiShoppingCart />
        </button>
        <Orders>{cart.length > 0 ? cart.length : ""}</Orders>
      </Icons>
      {openCart
        ? ReactDOM.createPortal(
            <Modal closeModal={handleCloseCart} />,
            document.getElementById("modal")
          )
        : null}
    </Container>
  );
}

export default Header;
