import styled from "styled-components";

export const Product = styled.div`
  height: 150px;
  width: 85%;
  margin: 0 auto;
  border-bottom: 1px solid rgba(103, 106, 112, 0.23);
  display: grid;
  grid-template-columns: 40% 60%;
  grid-template-rows: 1fr 1fr;
  position: relative;
`;

export const ProductInfo = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  button {
    font-weight: bold;
    font-size: 1.5rem;
    width: 35px;
    height: 35px;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  span {
    font-size: 1rem;

    display: inline-block;
    padding: 0 15px;
  }
  div {
    display: flex;
    align-items: center;
  }
`;

export const Image = styled.img`
  align-self: center;
  width: 70%;
  margin: 0 auto;
  height: 120px;
  grid-row-start: 1;
  grid-row-end: 3;
  border-radius: 10px;
  object-fit: cover;
`;

export const Title = styled.h2`
  margin-top: 15px;
  font-size: 1rem;
  font-weight: bold;
`;

export const Price = styled.span`
  font-size: 1rem;
  font-weight: bold;
`;

export const Delete = styled.button`
  position: absolute;
  top: 20px;
  right: 15px;
  border: none;
  outline: none;
  background-color: white;
  cursor: pointer;
  svg {
    width: 15px;
    height: 15px;
  }
`;
