import { useState } from "react";
import dataBase from "../dataBase";

const useThemeContext = () => {
  const [state, setState] = useState(dataBase);

  const handleAddProductQuantity = (item) => {
    const isAdded = state.productsQ.filter((product) => product.id === item.id);

    if (isAdded.length > 0) {
      const newProducts = state.productsQ.filter(
        (product) => product.id !== item.id
      );

      setState({
        ...state,
        productsQ: [...newProducts, item],
      });
    } else {
      setState({
        ...state,
        productsQ: [...state.productsQ, item],
      });
    }
  };

  const addToCart = (payload) => {
    const isAdded = state.cart.filter((product) => product.id === payload.id);

    if (isAdded.length > 0) {
      return setState(state);
    } else {
      setState({
        ...state,
        cart: [...state.cart, payload],
        productsQ: [
          ...state.productsQ,
          {
            id: payload.id,
            quantity: 1,
            price: payload.price,
            image: payload.image,
            title: payload.title,
          },
        ],
      });
    }
  };

  const deleteToCart = (payload) => {
    const newProductsQ = state.productsQ.filter(
      (product) => product.id !== payload.id
    );

    setState({
      ...state,
      cart: state.cart.filter((item) => item.id !== payload.id),
      productsQ: [newProductsQ],
    });
  };

  const addToFavs = (payload) => {
    setState({
      ...state,
      favs: [...state.favs, payload],
    });
  };

  const reloadProductsQ = () => {
    const reloadedProducts = state.productsQ.map((product) => ({
      ...product,
      quantity: 1,
    }));

    setState({
      ...state,
      productsQ: reloadedProducts,
    });
  };
  return {
    state,
    addToCart,
    addToFavs,
    deleteToCart,
    handleAddProductQuantity,
    reloadProductsQ,
  };
};

export default useThemeContext;
