import styled from "styled-components";

export const Background = styled.div`
  width: 100%;
  height: 100vh;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  top: 0;
  right: 0;
  left: 0;
`;

export const ModalContainer = styled.div`
  width: 35%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  background-color: white;
  z-index: 30;
  @media (max-width: 700px) {
    width: 100%;
  }
  button {
    cursor: pointer;
  }
`;

export const Head = styled.div`
  width: 100%;
  padding: 20px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  box-shadow: 2px 4px 15px -10px rgba(0, 0, 0, 0.5);
  h2 {
    font-size: 1.7rem;
    display: flex;
    align-items: center;
    svg {
      margin-right: 15px;
      width: 30px;
      height: 30px;
    }
  }
  button {
    padding: 15px;
    font-weight: bold;
    border: none;
    background-color: #cdd0cb;
  }
`;

export const Footer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px 30px;
  box-sizing: border-box;
  box-shadow: 2px -4px 15px -10px rgba(0, 0, 0, 0.5);
  div {
    display: flex;
    justify-content: space-between;
    span {
      font-weight: bold;
      font-size: 1rem;
    }
  }
  span {
    margin-top: 15px;
    display: block;
    text-align: center;
  }
  button {
    margin-top: 25px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    color: white;
    background-color: #11698e;
  }
`;

export const Cart = styled.div`
  width: 100%;
  height: 320px;
  overflow-y: scroll;
  position: relative;
  z-index: 10;
`;
