import styled from "styled-components";

export const HomeContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  h1 {
    text-align: center;
  }
`;

export const VideoContainer = styled.div`
  width: 100%;
  height: 230px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: normal;
  background-color: rgba(0, 0, 0, 0.3);
  @media (min-width: 600px) {
    height: 410px;
  }
`;

export const Video = styled.img`
  width: 100%;
  height: auto;
  object-fit: cover;
  height: 230px;
  border: none;
  outline: none;
  position: absolute;
  z-index: -20;
  @media (min-width: 600px) {
    height: 410px;
    top: 120px;
  }
`;

export const Title = styled.h1`
  color: white;
  font-size: 1.5rem;
  width: 100%;
  font-family: "Fira Code", monospace;
  @media (min-width: 600px) {
    font-size: 3rem;
  }
`;

export const TrendingNow = styled.div`
  margin-top: 50px;
  width: 100%;
  height: 520px;
  text-align: center;
  h2 {
    font-size: 1.2rem;
  }
  @media (min-width: 600px) {
    padding: 0 100px;
    height: 920px;
    box-sizing: border-box;
    h2 {
      margin-top: 80px;
      font-size: 2rem;
    }
  }
`;

export const TrendingCat = styled.div`
  margin-top: 20px;
  width: 100%;
  height: 400px;
  text-align: center;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  .principal-cat {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  @media (min-width: 600px) {
    height: 700px;
    margin-top: 80px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    .principal-cat {
      grid-row-start: 1;
      grid-row-end: 3;
    }
  }
`;

export const CatContainer = styled.div`
  overflow: hidden;
  cursor: pointer;
`;

export const CatImg = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  :hover {
    animation: cssAnimation 1s 16 ease;
  }

  @keyframes cssAnimation {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(1.383);
    }
  }
`;
