import React, { useContext } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

import { MdAddShoppingCart, MdFavoriteBorder } from "react-icons/md";

import ThemeContext from "../../ThemeContext";

const CardContainer = styled.div`
  position: relative;
  width: 140px;
  height: 260px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px 10px;
  margin: 0 auto;
  @media (min-width: 600px) {
    height: 350px;
    width: 220px;
  }
`;

const ImageContainer = styled.div`
  overflow: hidden;
`;

const Image = styled.img`
  width: 100%;
  height: 180px;
  object-fit: cover;
  @media (min-width: 600px) {
    height: 260px;
  }
  :hover {
    animation: cssAnimation 1s 16 ease;
  }

  @keyframes cssAnimation {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(1.383);
    }
  }
`;

const Title = styled.h2`
  font-size: 0.7rem;
  font-weight: bold;
  margin-top: 10px;
  @media (min-width: 600px) {
    font-size: 0.9rem;
  }
`;

const Autor = styled.span`
  font-size: 0.6rem;
  @media (min-width: 600px) {
    font-size: 0.8rem;
  }
`;
const Price = styled.span`
  font-weight: bold;
  font-size: 1.1rem;
  margin-top: 20px;
  @media (min-width: 600px) {
    font-size: 1.1rem;
  }
`;

const CartButton = styled.button`
  position: absolute;
  cursor: pointer;
  bottom: 9px;
  right: 9px;
  padding: 15px;
  border-radius: 50%;
  border: 2px solid gray;
  width: 15px;
  height: 15px;
  outline: none;
  @media (max-width: 600px) {
    display: none;
  }
  svg {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    bottom: 0;
  }
`;

const FavButton = styled.button`
  position: absolute;
  cursor: pointer;
  top: 21px;
  right: 15px;
  padding: 15px;
  border-radius: 50%;
  box-shadow: 0px 2px 10px -4px rgba(0, 0, 0, 0.5);
  width: 15px;
  height: 15px;
  outline: none;
  border: none;
  @media (max-width: 600px) {
    display: none;
  }
  svg {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    bottom: 0;
  }
`;

function Card({ item, detailsPage }) {
  const { addToFavs, addToCart } = useContext(ThemeContext);
  const handleAddFav = () => {
    addToFavs(item);
  };

  const handleAddToCart = () => {
    addToCart(item);
  };

  return (
    <Link to={detailsPage ? `${item.id}` : `details/${item.id}`}>
      <CardContainer>
        <ImageContainer>
          <Image src={item.image} alt="img" />
        </ImageContainer>
        <Title>{item.title}</Title>
        <Autor>De acerohernan</Autor>
        <Price>{`Desde ${item.price} US$`}</Price>
        <CartButton onClick={handleAddToCart}>
          <MdAddShoppingCart />
        </CartButton>
        <FavButton onClick={handleAddFav}>
          <MdFavoriteBorder />
        </FavButton>
      </CardContainer>
    </Link>
  );
}

export default Card;
