import React, { useContext } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

import ThemeContext from "../../ThemeContext";
import Layout from "../../components/Layout/index";
import ProductModal from "../../components/ProductModal/index";

const Title = styled.h1`
  text-align: center;
  padding: 60px 20px;
  box-sizing: border-box;
`;

const LinkStyled = styled(Link)`
  margin: 0 auto;
  text-decoration: underline;
  font-size: 1rem;
  color: blue;
  display: block;
  margin-top: 10px;
`;

const Subtotal = styled.span`
  display: block;
  text-align: center;
  margin-top: 10px;
`;
const Quantity = styled.span`
  display: block;
  text-align: center;
  margin-top: 80px;
`;

const ButtonPaypal = styled(Link)`
  display: block;
  width: 80%;
  max-width: 400px;
  height: 195px;
  background-image: url("https://assets.blog.whmcs.com/paypal-smart-checkout-buttons.png");
  background-position: center;
  background-size: cover;
  background-repeat: none;
  margin: 100px auto;
  @media (min-width: 400px) {
    height: 215px;
  }
  @media (min-width: 600px) {
    height: 235px;
  }
`;

const Checkout = () => {
  const { state } = useContext(ThemeContext);
  const { productsQ } = state;

  const totalQuantity = productsQ.reduce(
    (acc, product) => product.quantity + acc,
    0
  );
  const totalAmount = productsQ.reduce(
    (acc, product) => product.price * product.quantity + acc,
    0
  );
  return (
    <Layout>
      <Title>
        {`Tu carrito ${productsQ.length === 0 ? "est vaco" : ""}`}
        {productsQ.length === 0 && (
          <LinkStyled to="/">Ve a llenarlo </LinkStyled>
        )}
      </Title>

      <div>
        {productsQ.map((item) => (
          <ProductModal key={item.id} item={item} checkout />
        ))}
      </div>
      {productsQ.length > 0 && (
        <>
          <Quantity>{`Number of items: ${totalQuantity}`}</Quantity>
          <Subtotal>{`Total amount: $${totalAmount}`}</Subtotal>
          <ButtonPaypal to={"checkout/success"} />
        </>
      )}
    </Layout>
  );
};

export default Checkout;
