import styled from "styled-components";

export const Container = styled.div`
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 12px;
  box-sizing: border-box;
  box-shadow: 0px 0.1px 1px black;
  @media (min-width: 600px) {
    height: 120px;
    padding: 20px 80px;
  }
  .hidden-600px {
    @media (min-width: 600px) {
      display: none;
    }
  }
`;

export const Icons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: auto;
  width: 65px;
  button {
    border: none;
    background-color: white;
    outline: none;
    cursor: pointer;
  }
  @media (min-width: 600px) {
    width: 80px;
    svg {
      height: 30px;
      width: 30px;
    }
  }
  svg {
    width: 25px;
    height: 25px;
  }
`;

export const Orders = styled.span`
  position: absolute;
  border-radius: 50%;
  background-color: red;
  border: 2px solid white;
  width: 15px;
  height: 15px;
  right: 0;
  top: 9px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  color: white;
  font-size: 0.7rem;
  font-weight: bold;
  @media (min-width: 600px) {
    right: 68px;
    top: 40px;
  }
`;

export const Favs = styled.span`
  position: absolute;
  border-radius: 50%;
  background-color: red;
  border: 2px solid white;
  width: 15px;
  height: 15px;
  right: 45px;
  top: 9px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  color: white;
  font-size: 0.7rem;
  font-weight: bold;
  @media (min-width: 600px) {
    right: 128px;
    top: 40px;
  }
`;

export const Links = styled.div`
  width: 45%;
  display: none;
  align-items: center;
  justify-content: space-between;
  max-width: 350px;
  a {
    color: #222831;
  }
  [area-current] {
    text-decoration: underline;
  }

  @media (min-width: 600px) {
    display: flex;
  }
`;
